.warning {
    max-width: 80%;
    min-width: 30%;
    margin: 30px auto;
    background-color: crimson;
    color: white;
    border: 4px solid #8a0728;
    border-radius: 24px;
    height: 50px;
    font-size: 24px;
    display: flex;
    align-items: center;
    justify-content: center;
}